* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Rubik", sans-serif;
}

.main {
  height: 100vh;
  overflow-x: hidden;
  display: flex;
}

.left-side {
  width: 25%;
  display: flex;
  flex-direction: column;
}

.left-side__left-panel1 {
  height: 55%;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.left-panel1__logo-wrapper {
  position: absolute;
  display: flex;
  margin-top: 20px;
  margin-left: 40px;
  top: 0;
}

.logo-circle {
  width: 14px;
  height: 14px;
  margin-right: 4px;
  border-radius: 50%;
  background-color: #292929;
}

.logo-text {
  display: flex;
  align-items: center;
}

.logo {
  text-decoration: none;
  font-weight: bolder;
  margin-left: 5px;
  color: #1a1515;
  font-size: 12px;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.ls_panel__heading-wrapper {
  margin-left: 40px;
}

.heading-wrapper__main-heading {
  color: #181613;
  font-size: 3rem;
  line-height: 60px;
  font-weight: 300;
  margin: 20px 0 20px 0;
}

.heading-text {
  font-size: 0.9rem;
  color: rgba(51, 51, 51, 0.8);
  font-weight: 400;
  margin-right: 70px;
}

.left-side__left-panel2 {
  position: relative;
  background-color: #181612;
  height: 45%;
}

.ls-panel__slider-container {
  position: absolute;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.text-slide {
  position: relative;
}

.text-slide__content {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  width: 100%;
  height: 100%;
}

.text-slide_content__wrapper {
  display: flex;
  height: 100%;
  margin: 60px 45px 60px 40px;
  flex-direction: column;
  justify-content: space-between;
}

.top-content__heading {
  color: #998a72;
  font-size: 0.8rem;
  letter-spacing: 2px;
  margin-bottom: 12px;
}

.top-content__bottom-text {
  margin-bottom: 22px;
  font-size: 1.125rem;
}

.top-content__text {
  font-size: 1rem;
  line-height: 20px;
}

.bottom-content__text {
  font-size: 0.9rem;
  margin-left: 10px;
}

.text-slide__bottom-content {
  display: flex;
}

.swiper-buttons-wrapper {
  position: absolute;
  bottom: 0;
  display: flex;
  height: 50px;
  width: 100%;
  margin-left: 30px;
  margin-bottom: 45px;
}

.arrow-left {
  margin-right: 2px;
}

.arrow-right {
  margin-left: 2px;
}

#swiper-button {
  position: relative;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  border: 1px solid #000;
}

#swiper-button::after {
  content: "";
}

.swiper-button-next {
  margin-left: 2.5rem;
}

.swiper-pagination-fraction,
.swiper-pagination-custom,
.swiper-container-horizontal > .swiper-pagination-bullets {
  display: flex;
  justify-content: space-around;
  bottom: 0px;
  left: 0;
  width: 0;
  display: none;
}

.right-side {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 75%;
  position: relative;
}

.nav {
  position: relative;
  width: 100%;
  display: flex;
  align-items: center;
  height: 75px;
}

.nav__cart-wrapper {
  position: absolute;
  left: auto;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 11;
  display: flex;
  margin-right: 40px;
  align-items: center;
}

.cart-icon {
  cursor: pointer;
  margin-right: 20px;
  background-color: rgba(56, 152, 236, 0);
}

.menu-btn-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  cursor: pointer;
}

.menu-btn-line {
  height: 2px;
  margin: 2px 0;
  background-color: #000;
}

.menu-line1 {
  width: 20px;
}
.menu-line2 {
  width: 15px;
}
.menu-line3 {
  width: 16px;
}

.nav__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.nav__item-link {
  text-decoration: none;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 0.8rem;
  color: rgba(41, 41, 41, 0.4);
  margin-left: 30px;
  transition: color 0.25s ease-out;
}

.mobile-menu-link {
  display: none;
}

.nav__item:hover .nav__item-link {
  color: #000;
}

.nav__item:hover .nav__item__line {
  opacity: 1;
}

.nav__list {
  list-style: none;
  display: flex;
  height: 100%;
}

.nav__item__line {
  width: 1px;
  height: 10px;
  background-color: #000;
  position: absolute;
  top: 0;
  margin-left: 30px;
  opacity: 0;
  transition: opacity 0.25s ease-out;
}

.swiper-container {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.slide {
  height: 100vh;
  overflow: hidden;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.right-side__right-panel-wrapper {
  position: absolute;
  display: flex;
  height: 100%;
  width: 100%;
}

.rs-panel {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  flex: 1;
  z-index: 2;
}

.rs-panel__top__text {
  color: #fff;
  font-size: 0.8rem;
  line-height: 14px;
}

.rs-panel__bottom__text {
  color: #fff;
  font-size: 1rem;
  vertical-align: middle;
}

.rs-panel__top {
  display: flex;
  justify-content: flex-start;
  padding-left: 40px;
  margin-top: 20px;
}

.rs-panel__bottom {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  height: 150px;
  padding-left: 40px;
}

.rs-panel__heading {
  font-weight: bolder;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: #fff;
  font-size: 0.65rem;
  margin-bottom: 20px;
}

.line {
  background-color: #fff;
}

.active-panel {
  background-color: rgba(0, 0, 0, 0.45);
}

.active-panel-plus-icon {
  background-color: #fff;
}

.active-panel-line {
  background-color: #000;
}

.rs-panel__plus-icon {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 2px solid #fff;
  transition: background-color 0.25s ease-out;
  margin-bottom: 30px;
}

.plus-icon__vertical {
  position: absolute;
  width: 2px;
  height: 15px;
}

.plus-icon__horizontal {
  position: absolute;
  width: 15px;
  height: 2px;
}

.gradient {
  position: absolute;
  left: 0;
  right: 0;
  z-index: 1;
  height: 200px;
}

.right-side-gradient-top {
  top: 0;
  background-image: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0.8),
    hsla(0, 0%, 100%, 0)
  );
}

.right-side-gradient-bottom {
  bottom: 0;
  background-image: linear-gradient(
    180deg,
    hsla(0, 0%, 100%, 0),
    rgba(0, 0, 0, 0.8)
  );
}

.menu-toggle {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
  display: flex;
  width: 300px;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  transform: translateX(100%);
  transition: transform 0.15s linear;
}

.menu-toggle-active {
  transform: translateX(0);
}

.menu-toggle__container {
  display: flex;
  flex-direction: column;
}

.menu-link {
  margin: 15px 0 15px 0;
  color: #181612;
  text-align: center;
  letter-spacing: 2px;
  text-decoration: none;
  text-transform: uppercase;
}

.cart-toggle-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 20;
  visibility: hidden;
  background-color: rgba(0, 0, 0, 0.8);
  transition: visibility 300ms, opacity 300ms ease;
  opacity: 0;
}

.cart-toggle-wrapper-active {
  visibility: visible;
  opacity: 1;
}

.cart-toggle {
  display: flex;
  width: 100%;
  max-width: 480px;
  min-width: 320px;
  flex-direction: column;
  overflow: auto;
  background-color: #fff;
  box-shadow: 0px 5px 25px 0px rgb(0 0 0 / 25%);
}

.cart-toggle__header-container {
  position: relative;
  display: flex;
  padding: 16px 24px 16px 24px;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #e6e6e6;
}

.cart-toggle__items-container {
  display: flex;
  padding-top: 100px;
  padding-bottom: 100px;
  justify-content: center;
  align-items: center;
}

.cart-toggle__header {
  font-size: 1.2rem;
  line-height: 24px;
}

.close-icon {
  width: 15px;
  height: 15px;
  cursor: pointer;
}

.menu-toggle__mobile-nav {
  display: none;
}

@media screen and (max-width: 1100px) {
  .main {
    flex-direction: column;
    height: auto;
  }
  .left-side {
    width: 100%;
  }
  .ls_panel {
    height: 50vh;
  }
  .nav {
    position: fixed;
    left: 0%;
    top: 0%;
    right: 0%;
    bottom: auto;
    z-index: 99;
    display: block;
    background-color: hsla(0, 0%, 86.7%, 0);
  }
  .right-side {
    width: 100%;
  }
  .right-side__right-panel-wrapper {
    position: relative;
    flex-direction: column;
  }
  .rs-panel {
    height: 50vh;
    flex: 0 auto;
    background-position: 50% 50%;
    background-size: cover;
  }

  .rs-panel1 {
    background-image: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0.4),
        rgba(0, 0, 0, 0.4)
      ),
      url("../assets/im1.png");
  }
  .rs-panel2 {
    background-image: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0.4),
        rgba(0, 0, 0, 0.4)
      ),
      url("../assets/im2.png");
  }
  .rs-panel3 {
    background-image: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0.4),
        rgba(0, 0, 0, 0.4)
      ),
      url("../assets/im3.png");
  }
  .rs-panel4 {
    background-image: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0.4),
        rgba(0, 0, 0, 0.4)
      ),
      url("../assets/im4.png");
  }
  .rs-panel5 {
    background-image: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0.4),
        rgba(0, 0, 0, 0.4)
      ),
      url("../assets/im5.png");
  }
  .swiper-buttons-wrapper {
    display: none;
  }
  .nav__list {
    visibility: hidden;
  }

  .left-panel1__logo-wrapper {
    margin-top: 31px;
    z-index: 30;
  }
  .menu-toggle {
    left: 0;
    z-index: 10;
    height: 100vh;
    width: 100%;
  }
  .menu-toggle__mobile-nav {
    display: flex;
    flex-direction: column;
  }
  .mobile-menu-link {
    display: block;
  }
  .menu-link::after {
    display: block;
    margin-bottom: 10px;
    content: "";
    width: 250px;
    height: 1px;
    background-color: #e5e5e5;
  }
}

@media screen and (max-width: 411px) {
  .cart-toggle {
    width: 340px;
  }
  .menu-toggle__container {
    padding-top: 50px;
  }
}

@media screen and (max-width: 320px) {
  .cart-toggle {
    width: 245px;
  }
  .menu-toggle__container {
    padding-top: 50px;
  }
  .top-content__text {
    margin-bottom: 10px;
  }
}

@media screen and (max-width: 281px) {
  .cart-toggle {
    max-width: 200px;
  }
}
